import React, { useState, useEffect } from 'react';
import { Snackbar, Button } from '@material-ui/core';
import AddIcon from '@material-ui/icons/Add';
import Card from '../components/card';
import NoCard from '../components/nocard';
import Layout from './layout';
import api from '../api';

const Main = () => {
  const [error, setError] = useState(false);
  const [limit] = useState(12);
  const [skip, setSkip] = useState(0);
  const [size, setSize] = useState(0);
  const [cards, setCards] = useState([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    loadCards();
  }, [])

  const loadCards = () => {
    setLoading(true);
    api.getProducts(skip, limit).then((data) => {
      setLoading(false);
      if (data.error) {
        setError(data.error);
      } else {
        console.log(data)
        setCards(data.data);
        setSize(data.size);
        setSkip(0);
      }
    });
  };

  const loadMore = () => {
    let toSkip = skip + limit;
    api.getProducts(toSkip, limit).then((data) => {
      if (data.error) {
        setError(data.error);
      } else {
        console.log(cards, data)
        setCards([...cards, ...data.data]);
        setSize(data.size);
        setSkip(toSkip);
      }
    });
  };

  const loadMoreButton = () => {
    return (
      size > 0 &&
      size >= limit && (
        <Button onClick={loadMore} variant='contained' className='more-btn'>
          more <AddIcon />
        </Button>
      )
    );
  };

  const shopItemClicked = (_id, share) => {
    api.shareProduct(_id, share).then((data) => {
      if (!data || data.error) {
        setError(data.error);
      } else {
        const idx = cards.findIndex(itm => itm._id === _id)
        if (idx < 0) return
        cards[idx].shared = data.shared;
        setCards([...cards]);
      }
    });
  }

  return (
    <Layout>
      <div>
        <div className='d-flex flex-wrap justify-content-center'>
          {cards.map((card, i) => (
            <div key={i} className={'shop-item ' + (card.shared ? 'selected' : '' )} onClick={() => shopItemClicked(card._id, 1 - card.shared)}>
              <Card card={card}/>
            </div>
          ))}
        </div>
        {loadMoreButton()}
      </div>
      {!cards.length &&<NoCard loading={loading}/>}
      <Snackbar open={!!error} autoHideDuration={6000} anchorOrigin={{vertical: 'top', horizontal: 'center'}} onClose={() => setError('')}>
        <div className="error-msg">{error}</div>
      </Snackbar>
    </Layout>
  );
};

export default Main;
