import React, { useState } from 'react';
import { useDispatch } from "react-redux"
import { Snackbar } from '@material-ui/core';
import { actions } from '../redux/_actions';
import Layout from './layout';

const Signin = () => {
  const dispatch = useDispatch();

  const [value, setValue] = useState('');
  const [error, setError] = useState(false);

  const signin = () => {
    var pattern = /^\w+@[a-zA-Z_]+?\.[a-zA-Z]{2,3}$/; 
    if (!value.match(pattern)) {
      setError('Input valid Email')
      return;
    }

    dispatch(actions.signin({ email: value }))
  }

  return (
    <Layout>
      <div className='sign-in'>
        <div className="input-label">
          Sign in with your email
        </div>
        <div className="input-text">
          <input value={value} onChange={(e) => setValue(e.target.value)}/>
        </div>
        <div className="input-button" onClick={signin}>
          Sign In
        </div>
      </div>
      <Snackbar open={!!error} autoHideDuration={6000} anchorOrigin={{vertical: 'top', horizontal: 'center'}} onClose={() => setError('')}>
        <div className="error-msg">{error}</div>
      </Snackbar>
    </Layout>
  )
}

export default Signin;
